
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 18px;
  line-height: 1.5;
}
*,
:after,
:before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
a {
  text-decoration: none;
}
h1 {
  text-align: center;
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 30px auto;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 400px;
}
.heading {
  line-height: 1;
  font-size: 120%;
  width: 100%;
  padding: 20px;
  text-align: center;
  display: block;
  color: #111;
}
.button-large {
  line-height: 1;
  font-size: 100%;
  width: 100%;
  padding: 20px;
  border: 1px solid white;
  border-radius: 4px;
  cursor: pointer;
  background: whitesmoke;
  text-align: center;
  display: block;
  color: #3f9de1;
}
.button-large-on {
  background: #3f9de1;
  color: white;
}
.button-large-disabled {
  background: white;
  color: #777;
  cursor: default;
}
.button-large-off {
  background: #777;
  color: white;
}
.button-large-end {
  background: #cd3b07;
  color: white;
}
.button-large-min {
  background: white;
  font-size: 100%;
  color: #3f9de1;
  border: 1px solid #a5cfe7;
}
.clear {
  clear: both;
  display: block;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0;
}
.anim-list-move {
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
.hidden {
  visibility: hidden;
}



